import { stringify } from './stringify';
export function makeInjectError(name) {
    return function injectError(token) {
        const error = new Error(`No provide for \`${stringify(token)}\`!`);
        error.name = name;
        return error;
    };
}
export function madeProvideScopeError(name) {
    return function provideError(token) {
        const error = new Error(`Can not found provide scope \`${stringify(token)}\`!`);
        error.name = name;
        return error;
    };
}
